  const handleCancelStart = async () => {
    if (!tempEstimateNo) return;
    if (window.confirm('  ?    ""  .')) {
      try {
        const response = await fetch(`http://192.168.0.14:5135/api/estimate/sheets/${tempEstimateNo}/cancel-start`, {
          method: 'POST',
        });

        if (response.ok) {
          alert('  .');
          setCurrentStatus('');
          loadExistingData(); //  
        } else {
          const errorText = await response.text();
          alert(`  : ${errorText}`);
        }
      } catch (error) {
        console.error('    :', error);
        alert('     .');
      }
    }
  };

