import React, { useState, useEffect } from 'react';
import './DashboardPages.css';
import './MembershipRequests.css';
import { getPendingApprovals, approveUser, UserListResponseDto } from '../../api/userManagement';

interface MembershipRequest {
  userID: string;
  companyName: string;
  name: string;
  email: string;
  businessNumber: string;
  address: string;
  companyPhone: string;
  department: string;
  position: string;
  phoneNumber: string;
}

const MembershipRequestsPage: React.FC = () => {
  const [requests, setRequests] = useState<MembershipRequest[]>([]);
  const [selectedRequest, setSelectedRequest] = useState<MembershipRequest | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [confirmAction, setConfirmAction] = useState<'approve' | 'reject' | null>(null);
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');

  //     
  useEffect(() => {
    loadPendingRequests();
  }, []);

  const loadPendingRequests = async () => {
    try {
      setLoading(true);
      setError(null);
      const data = await getPendingApprovals();
      const requestData: MembershipRequest[] = data.map(request => ({
        userID: request.userID,
        companyName: request.companyName,
        name: request.name,
        email: request.email,
        businessNumber: request.businessNumber,
        address: request.address,
        companyPhone: request.companyPhone,
        department: request.department,
        position: request.position,
        phoneNumber: request.phoneNumber
      }));
      setRequests(requestData);
    } catch (err) {
      console.error('    :', err);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  const handleRequestClick = (request: MembershipRequest) => {
    setSelectedRequest(request);
  };

  const handleApprove = () => {
    setConfirmAction('approve');
    setShowConfirmModal(true);
  };

  const handleReject = () => {
    setConfirmAction('reject');
    setShowConfirmModal(true);
  };

  const handleConfirmAction = async () => {
    if (!selectedRequest || !confirmAction) return;

    try {
      if (confirmAction === 'approve') {
        await approveUser(selectedRequest.userID);
        setSuccessMessage('     .');
      } else {
        //    
        // await deleteUser(selectedRequest.userID);
        setSuccessMessage('     .');
      }

      setShowConfirmModal(false);
      setShowSuccessModal(true);
      
      //  
      await loadPendingRequests();
      setSelectedRequest(null);
    } catch (err: any) {
      console.error('  :', err);
      setError(err.response?.data?.message || '  .');
      setShowConfirmModal(false);
    }
  };

  const handleCloseSuccessModal = () => {
    setShowSuccessModal(false);
    setSuccessMessage('');
  };

  if (loading) {
    return (
      <div className="membership-requests-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>    ...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="membership-requests-page">
        <div className="error-container">
          <p className="error-message">{error}</p>
          <button onClick={loadPendingRequests} className="retry-btn"> </button>
        </div>
      </div>
    );
  }

  return (
    <div className="membership-requests-page">
      <div className="page-header">
        <h1> </h1>
      </div>

      <div className="content-container">
        {/* :   */}
        <div className="requests-list">
          <h2>  </h2>
          <div className="table-container">
            <table className="requests-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th></th>
                  <th> </th>
                  <th> </th>
                </tr>
              </thead>
              <tbody>
                {requests.map((request) => (
                  <tr 
                    key={request.userID}
                    onClick={() => handleRequestClick(request)}
                    className={`request-row ${selectedRequest?.userID === request.userID ? 'selected' : ''}`}
                  >
                    <td>{request.userID}</td>
                    <td>{request.companyName}</td>
                    <td>{request.name}</td>
                    <td>{request.email}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <p className="table-hint">     -   /   </p>
        </div>

        {/* :   */}
        <div className="request-detail">
          <h2>   </h2>
          {selectedRequest ? (
            <div className="detail-content">
              <div className="detail-row">
                <label>:</label>
                <span>{selectedRequest.companyName}</span>
              </div>
              <div className="detail-row">
                <label>:</label>
                <span>{selectedRequest.businessNumber}</span>
              </div>
              <div className="detail-row">
                <label>:</label>
                <span>{selectedRequest.address}</span>
              </div>
              <div className="detail-row">
                <label>:</label>
                <span>{selectedRequest.companyPhone}</span>
              </div>
              <div className="detail-row">
                <label> :</label>
                <span>{selectedRequest.name}</span>
              </div>
              <div className="detail-row">
                <label> :</label>
                <span>{selectedRequest.department}</span>
              </div>
              <div className="detail-row">
                <label> :</label>
                <span>{selectedRequest.position}</span>
              </div>
              <div className="detail-row">
                <label> :</label>
                <span>{selectedRequest.phoneNumber}</span>
              </div>
              <div className="detail-row">
                <label> :</label>
                <span>{selectedRequest.email}</span>
              </div>

              <div className="action-buttons">
                <button className="btn-approve" onClick={handleApprove}>
                  
                </button>
                <button className="btn-reject" onClick={handleReject}>
                  
                </button>
              </div>
            </div>
          ) : (
            <div className="no-selection">
              <p>    .</p>
            </div>
          )}
        </div>
      </div>

      {/*   */}
      {showConfirmModal && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3>{confirmAction === 'approve' ? '?' : '?'}</h3>
            <p>
              {confirmAction === 'approve' 
                ? '    ?' 
                : '    ?'}
            </p>
            <div className="modal-actions">
              <button 
                className="btn-cancel" 
                onClick={() => setShowConfirmModal(false)}
              >
                
              </button>
              <button 
                className={confirmAction === 'approve' ? 'btn-approve' : 'btn-reject'}
                onClick={handleConfirmAction}
              >
                {confirmAction === 'approve' ? '' : ''}
              </button>
            </div>
          </div>
        </div>
      )}

      {/*   */}
      {showSuccessModal && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3>!</h3>
            <p>{successMessage}</p>
            <div className="modal-actions">
              <button className="btn-close" onClick={handleCloseSuccessModal}>
                
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MembershipRequestsPage; 