  const handleDownloadAttachment = useCallback(async (attachmentId: number, fileName: string) => {
    try {
      const response = await axios.get(`/api/estimate/attachments/${attachmentId}/download`, {
        responseType: 'blob'
      });

      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', fileName);
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
    } catch (error) {
      console.error('  :', error);
      alert('  .');
    }
  }, []);

  //      ( - PDF)
  const handleDownloadManagerFile = useCallback(async (file: any) => {
    try {
      if (!file.fileName.toLowerCase().endsWith('.pdf')) {
        alert('PDF    .');
        return;
      }
      const response = await fetch(`http://192.168.0.14:5135/api/estimate/attachments/${file.attachmentID}/download`);
      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = file.fileName;
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(a);
      } else {
        alert('  .');
      }
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  }, []);

